<template>
  <v-container fluid pa-0>
    <v-col style="padding-bottom: 0px;">
    <v-hover v-slot="{ hover }">
      <v-card
        :elevation="hover ? 5 : 3"
        :class="{ 'on-hover': hover }"
      >
        <v-card-text>
          <v-row no-gutters>
          <div class="pa-3">
            <a :href="'#/kunlun/' + station.name.toLowerCase()">
              <h1 class="font-weight-light">{{ station.name }}</h1>
            </a>
          </div>
          <v-spacer></v-spacer>
          <v-btn class="pa-3" style="margin-top: 5px;"
            icon
            color="primary"
            @click="toRouter(station.name)"
          >
            <v-icon>settings</v-icon>
          </v-btn>
          </v-row>
        </v-card-text>
      </v-card>
    </v-hover>
    </v-col>
  </v-container>
</template>

<script>
  export default {
    name: 'StationPanel',
    props: ['station'],

    data: () => ({

    }),
    methods: {
      toRouter (path) {
      this.$router.push('/set-container/' + path)
    }
    }
  }
</script>
<style scoped>
a:link {
    background-color: transparent;
    text-decoration: none;
}
a:hover {
    color: red;
    background-color: transparent;
    text-decoration: underline;
}
</style>